html,
body {
  width: 100%;
  height: 100%;
  min-width: 1024px;
  min-height: 576px;
  margin: 0;
  padding: 0;
}
body {
  font-family: 'Oxygen', sans-serif;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Fjalla One', sans-serif;
}
.viewport {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.viewport .page {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  transition: 1.0s;
}
.viewport .page.up {
  top: -100%;
}
.viewport .page.down {
  top: 100%;
}
.viewport .page .row,
.viewport .page .page-left,
.viewport .page .page-right {
  height: 100%;
}
.viewport .page .page-left,
.viewport .page .page-right {
  padding: 0;
}
#overview .page-left {
  background: #1c4b77;
  color: white;
  font-size: 0.9em;
  position: relative;
}
#overview .page-left .top {
  width: 100%;
  height: 75%;
  padding: 2em;
}
#overview .page-left .top h1,
#overview .page-left .top p {
  padding: 0;
  margin: 0.5em 0;
}
#overview .page-left .top h1 {
  margin-top: 0;
  font-size: 3.5em;
}
#overview .page-left .top h1 small {
  color: inherit;
  font-size: 0.625em;
}
#overview .page-left .top p {
  margin-bottom: 0;
  text-align: justify;
}
#overview .page-right {
  background: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#overview .page-right #chart {
  width: 95%;
  height: 70%;
}
#overview .page-right #chart text {
  font-family: "Oxygen";
}
#overview .page-right #chart-hover {
  padding: 1em 0;
  opacity: 0;
  font-weight: bolder;
}
#overview .page-right #chart-hover.active {
  opacity: 1;
}
#details .page-left {
  background: #1c4b77;
  color: white;
  text-align: center;
  position: relative;
}
#details .page-left #tips {
  font-size: 1.1em;
  padding: 1em;
  text-align: justify;
}
#details .page-left .legend-container {
  background: #deebf7;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
}
#details .page-left img.legend {
  width: 100%;
  height: auto;
  margin: 0;
  padding: 0;
}
#details .page-right {
  background: white;
}
#details .page-right .btn-back {
  margin: 1em;
  margin-bottom: 0;
  padding: 0.5em;
}
.heatmap-list {
  margin: 0;
  padding: 0;
}
.heatmap {
  list-style-type: none;
  padding-bottom: 1em;
}
.heatmap h1 {
  text-align: center;
  margin: 0 auto;
}
.heatmap table {
  empty-cells: show;
  table-layout: fixed;
  margin: 0 auto;
}
.heatmap table tr:first-child th,
.heatmap table td {
  width: 50px;
  height: 36px;
}
.heatmap table tr:first-child th:first-child,
.heatmap table td:first-child {
  width: 250px;
}
.heatmap table th {
  text-align: center;
}
.heatmap table thead tr:first-child th:nth-child(2),
.heatmap table th:nth-child(5),
.heatmap table td:nth-child(6) {
  border-right: 10px solid #ffffff;
}
.heatmap table th,
.heatmap table td {
  margin: 0;
  padding: 0;
}
.heatmap table th.hide-col,
.heatmap table td.hide-col,
.heatmap table th.hide-ujian,
.heatmap table td.hide-ujian {
  background: transparent;
  color: transparent;
  width: 0;
  height: 0;
}
.popover {
  font-family: 'Oxygen', sans-serif;
  font-size: 0.9em;
}
.datavis-control {
  text-align: center;
}
