.trivia {
  height: 100px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background: white;
}
.trivia .trivia-title {
  position: absolute;
  top: -3em;
  left: 0;
  width: 100%;
  padding: 1em;
  text-align: center;
  font-size: 1.5em;
  font-variant: small-caps;
  font-weight: bolder;
}
#carousel-trivia {
  height: 100%;
}
#carousel-trivia .carousel-inner,
#carousel-trivia .item {
  height: 100%;
}
#carousel-trivia .item h3 {
  color: #1c4b77;
  font-size: 2em;
  text-shadow: none;
  margin: 0;
  padding: 0;
}
#carousel-trivia .item p {
  color: #000000;
  font-size: 1em;
  text-shadow: none;
  margin: 0;
  padding: 0;
}
#carousel-trivia .carousel-caption {
  margin: 0 auto;
  padding: 0;
}
#carousel-trivia .carousel-control {
  color: #1c4b77;
  opacity: 1.0;
  text-shadow: none;
  transition: text-shadow 0.5s;
}
#carousel-trivia .carousel-control.left {
  background: linear-gradient(to right, white, transparent);
}
#carousel-trivia .carousel-control.right {
  background: linear-gradient(to left, white, transparent);
}
#carousel-trivia .carousel-control .glyphicon {
  font-size: 1em;
}
#carousel-trivia .carousel-control:hover,
#carousel-trivia .carousel-control:active {
  text-shadow: 0px 0px 10px #408cd2;
}
